:root {
  // base
  --font-family: "Unbounded", sans-serif;
  --second-family: "Orbitron", sans-serif;
  --content-width: 1290px;
  --container-offset: 30px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --bg-color: #0c0f1d;
  --text-color: #f7f7f7;
  --subtext-color: #f7f7f780;
  --subaccent-color: #03fc9d;
  --accent-color: #c684f6;
  --button-bg-color: #292c3b;
  --alert-color: #ff004c;
  --official-color: #4ca3ffff;
  --ika-color: #f1285c;
  --wal-color: #99efe4;
}

[data-theme="light"] {
  --bg-color: #f7f7f7;
  --text-color: #0c0f1d;
  --subtext-color: #0b0e1bbd;
  --button-bg-color: #e7e9f3;
}
