.header {
  position: relative;
  top: 0;
  padding-top: 20px;
  padding-bottom: 20px;

  &__nav {
    width: 100%;
  }

  &__top-content {
    justify-content: space-between;
    margin-bottom: 20px;

    @media (width <=510px) {
      flex-direction: column;
      margin-bottom: 10px;
      row-gap: 10px;
    }
  }

  &__bottom-content {
    column-gap: 20px;
    justify-content: end;

    @media (width <=510px) {
      flex-wrap: wrap;
      row-gap: 10px;
    }
  }

  &__link-logo {
    border-radius: 0;
    padding: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 32px;
    color: var(--subaccent-color);
    background-color: transparent;
  }

  &__error {
    position: absolute;
    top: 7px;
    font-size: 10px;
    color: var(--alert-color);
    opacity: 0;
    transition: all .3s ease-in-out;
  }

  &__form {
    width: 25%;

    @media (width <=1200px) {
      width: 42%;
    }

    @media (width <=833px) {
      width: 61%;
    }

    @media (width <=661px) {
      width: 214.5px;
    }

    @media (width <=510px) {
      align-self: end;
      width: 90%;
    }
  }

  &__search {
    outline: none;
    border: 1px solid var(--button-bg-color);
    border-radius: 8px;
    padding: 10px 15px;
    width: 100%;
    min-width: 242px;
    font-size: 14px;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      border: 1px solid var(--subaccent-color);
    }

    &:focus {
      border: 1px solid var(--subaccent-color);
    }

    @media (width <=661px) {
      min-width: 100%;
    }
  }

  &__btn {
    outline: none;
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    transition: all .3s ease-in-out;

    @media (width <=510px) {
      width: fit-content;
    }
  }

  &__btn-theme {
    display: flex;
    align-items: center;
    padding: 10px 9px 10px 11px;
    background-color: var(--button-bg-color);

    @include hover {
      background-color: var(--text-color);

      .header__icon {
        fill: var(--bg-color);
      }
    }

    &:focus {
      background-color: var(--text-color);

      .header__icon {
        fill: var(--bg-color);
      }
    }
  }

  &__btn-contacts {
    color: var(--text-color);
    background-color: var(--button-bg-color);

    @include hover {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    &:focus {
      outline: none;
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }
  }

  &__btn-wal {
    color: var(--bg-color);
    background-color: var(--wal-color);

    @include hover {
      opacity: .3;
      cursor: not-allowed;

      .header__icon {
        opacity: .3;
      }
    }
  }

  &__btn-ika {
    color: var(--bg-color);
    background-color: var(--ika-color);

    @include hover {
      opacity: .3;
      cursor: not-allowed;

      .header__icon {
        opacity: .3;
      }
    }
  }

  &__icon {
    width: 20px;
    height: 20px;
    fill: var(--text-color);
    transition: all .3s ease-in-out;
  }
}
