.footer {
  position: relative;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;

  @media (width <=661px) {
    flex-direction: column-reverse;
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 0;
    width: calc(100% - 60px);
    height: 1px;
    background-color: var(--accent-color);
    transform: translateX(-50%);
  }

  &__link {
    border-radius: 0;
    padding: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 16px;
    color: var(--subaccent-color);
    background-color: transparent;
  }

  &__text {
    margin: 0;
    font-size: 12px;
    color: var(--subtext-color);

    @media (width <=661px) {
      text-align: center;
    }
  }

  &__donate-btn {
    position: relative;
    border: none;
    border-radius: 8px;
    width: 261px;
    height: 38px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--bg-color);
    background-color: var(--alert-color);
    transition: all 0.3s ease-in-out;
    cursor: pointer;

    @include hover {
      background-color: var(--subaccent-color);
    }

    @media (width <=661px) {
      margin-bottom: 20px;
    }
  }

  &__donate-text {
    position: absolute;
    left: 50%;
    top: 50%;
    white-space: nowrap;
    opacity: 0;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in-out;
    pointer-events: none;

    &--default {
      opacity: 1;
      pointer-events: auto;
    }

    &--wallet {
      display: flex;
      align-items: center;
    }
  }

  &__donate-btn.show-wallet &__donate-text--default,
  &__donate-btn.show-copied &__donate-text--default {
    opacity: 0;
    pointer-events: none;
  }

  &__donate-btn.show-wallet &__donate-text--wallet {
    opacity: 1;
    pointer-events: auto;
  }

  &__donate-btn.show-copied &__donate-text--copied {
    opacity: 1;
    pointer-events: auto;
  }

  &__icon {
    margin-left: 5px;
    width: 16px;
    height: 16px;
    fill: #4da2ff;
  }
}

.show-wallet,
.show-copied {
  background-color: var(--subaccent-color);
}
