.project {
  flex-direction: column;
  padding-bottom: 60px;

  &__title {
    margin: 0 auto;
    margin-bottom: 20px;
    font-weight: 900;
    font-size: 36px;
    text-transform: uppercase;
    color: var(--text-color);
    transition: all .3s ease-in-out;

    @media (width <=833px) {
      font-weight: 600;
    }
  }

  &__filter {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    margin-bottom: 30px;

    &:nth-child(2) {
      align-items: center;
    }
  }

  &__list {
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 10px;
    gap: 10px;

    @media (width <=661px) {
      flex-wrap: nowrap;
      justify-content: flex-start;
      overflow: scroll;
      width: 100%;
    }
  }

  &__btn {
    outline: none;
    border-radius: 8px;
    padding: 11px 30px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    @media (width <=661px) {
      min-width: max-content;
    }

    &--all {
      background-color: var(--accent-color);
    }

    &.active {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__social-wrapper {
    margin-top: 20px;
    border-top: 2px solid var(--accent-color);
    padding-top: 20px;
  }

  &__card-list {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
    gap: 30px;
  }

  &__card-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    outline: none;
    border: 2px solid var(--accent-color);
    border-radius: 8px;
    padding: 20px;
    width: 300px;
    height: 415px;
    color: var(--text-color);
    transition: all .3s ease-in-out;

    &:focus {
      border-color: var(--subaccent-color);

      .project__card-logo {
        border-color: var(--subaccent-color);
      }

      .project__card-category {
        border-color: var(--subaccent-color);
      }

      .project__card-descr {
        border-color: var(--subaccent-color);
      }
    }

    &.hidden {
      display: none;
    }
  }

  &__card-logo {
    border: 2px solid var(--accent-color);
    border-radius: 100%;
    width: 60px;
    height: 60px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: all .3s ease-in-out;
  }

  &__card-name {
    margin-bottom: 10px;
    font-weight: 900;
    text-transform: uppercase;
  }

  &__card-wrapper {
    position: relative;
    margin-bottom: 10px;
  }

  &__card-category {
    margin-bottom: 10px;
    border: 2px solid var(--accent-color);
    border-radius: 8px;
    padding: 4px 6px 3px;
    max-width: fit-content;
    font-size: 10px;
    text-transform: uppercase;
    transition: all .3s ease-in-out;
  }

  &__card-drop {
    position: absolute;
    right: 0;
    top: 0;
    border: 2px solid var(--alert-color);
    border-radius: 8px;
    padding: 4px 6px 3px;
    max-width: fit-content;
    font-size: 10px;
    text-transform: uppercase;
    transition: all .3s ease-in-out;
  }

  &__card-descr {
    min-height: 110px;
    font-size: 12px;
    line-height: 2;
    transition: all .3s ease-in-out;
  }

  &__card-btn {
    align-self: center;
    outline: none;
    border-radius: 8px;
    padding: 20px 40px;
    min-width: 191px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }

    &:focus {
      color: var(--bg-color);
      background-color: var(--subaccent-color);
    }
  }

  &__social-icon {
    width: 24px;
    height: 24px;
    fill: var(--text-color);
  }

  &__social-item {
    outline: none;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: 2px solid var(--text-color);
    border-radius: 8px;
    padding: 8px;
    width: 100%;
    height: 100%;
    transition: all .3s ease-in-out;

    @include hover {
      border: 2px solid var(--subaccent-color);
      background-color: var(--subaccent-color);

      .project__social-icon {
        fill: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }

    &:focus {
      border: 2px solid var(--subaccent-color);
      background-color: var(--subaccent-color);

      .project__social-icon {
        fill: var(--bg-color);
        transition: all .3s ease-in-out;
      }
    }
  }

  &__sort {
    &-list {
      column-gap: 10px;
      justify-content: space-between;
      width: 100%;
      max-width: 75%;
      white-space: nowrap;

      @media (width <=1199px) {
        max-width: 100%;
      }

      @media (width <=949px) {
        overflow: scroll;
      }
    }

    &-btn {
      outline: none;
      border: 2px solid var(--subaccent-color);
      border-radius: 8px;
      padding: 9px 30px;
      font-size: 14px;
      text-transform: uppercase;
      color: var(--text-color);
      background-color: var(--button-bg-color);
      transition: all .3s ease-in-out;

      @include hover {
        color: var(--bg-color);
        background-color: var(--subaccent-color);
      }

      &:focus {
        color: var(--bg-color);
        background-color: var(--subaccent-color);
      }
    }
  }
}
