.hero {
  column-gap: 50px;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 60px;
  color: var(--text-color);

  @media (width <=833px) {
    flex-direction: column-reverse;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__descr {
    max-width: 30%;

    @media (width <=1199px) {
      max-width: 40%;
    }

    @media (width <=833px) {
      max-width: 100%;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-size: 36px;
    transition: all .3s ease-in-out;
  }

  &__text {
    margin: 0;
    margin-bottom: 20px;
    font-size: 14px;
    color: var(--subtext-color);
    transition: all .3s ease-in-out;
  }

  &__count {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    padding: 15px 30px;
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    @media (width <=833px) {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }

  &__number {
    margin-bottom: 10px;
    font-weight: 900;
    font-size: 24px;
    color: var(--accent-color);
  }

  &__name {
    position: relative;
    margin-bottom: 10px;
    outline: none;
    font-weight: 900;
    font-size: 24px;
    color: var(--accent-color);
    transition: all .3s ease-in-out;
  }

  &__total {
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
    color: var(--subtext-color);
    transition: all .3s ease-in-out;
  }

  &__last {
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
    color: var(--subtext-color);
  }

  &__logo {
    border-radius: 8px;
    width: 67%;
    background-image: url("../img/svg/banner-sui.svg");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: var(--accent-color);

    @media (width <=833px) {
      margin-bottom: 20px;
      width: 100%;
      height: 340px;
    }

    @media (width <=480px) {
      height: 240px;
    }
  }

  &__icon {
    position: absolute;
    top: -5px;
    width: 14px;
    height: 14px;
    fill: #FF782F;
  }

  &__btn {
    outline: none;
    border: 1px solid var(--button-bg-color);
    border-radius: 8px;
    padding: 15px 30px;
    width: 100%;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--button-bg-color);
    transition: all .3s ease-in-out;

    @include hover {
      border: 1px solid var(--subaccent-color);

      .hero__name {
        color: var(--subaccent-color);
      }

      .hero__total {
        color: var(--text-color);
      }
    }

    &:focus {
      border: 1px solid var(--subaccent-color);

      .hero__name {
        color: var(--subaccent-color);
      }

      .hero__total {
        color: var(--text-color);
      }
    }
  }

  &__wrapper {
    gap: 30px;

    @media (width <=833px) {
      display: flex;
    }

    @media (width <=670px) {
      flex-direction: column;
    }
  }
}
